html{box-sizing:border-box;font-family:"Courier New", sans-serif}*,*:before,*:after{box-sizing:inherit}body{margin:0;padding:0;min-width:320px;animation:color-change-3x 6s linear infinite alternate both}ul,ol{margin:0;padding:0;list-style:none}a{text-decoration:none}input[type=range]{-webkit-appearance:none}input[type=range]::-webkit-slider-runnable-track{width:300px;height:5px;background:lightblue;border:0.1em solid #000000;border-radius:0.5em}input[type=range]::-webkit-slider-thumb{-webkit-appearance:none;height:1em;width:1em;border:0.1em solid #000000;border-radius:50%;background:#1FA2FF;margin-top:-0.4em}input[type=range]:focus{outline:none}@keyframes color-change-3x{0%{background:#A6FFCB}50%{background:#12D8FA}100%{background:#1FA2FF}}.intro{position:fixed;z-index:5;top:0;width:100vw;height:100vh;display:flex;flex-direction:column;justify-content:center;align-items:center;background:linear-gradient(to right, #A6FFCB, #12D8FA, #1FA2FF);opacity:1;transition:all 0.7s}.intro_faded{opacity:0}.intro_inactive{display:none}.intro__heading{text-transform:uppercase;font-size:3em}.intro__paragraph{margin:0 0 1.5em 0;text-align:center;text-transform:lowercase;font-size:1.5em}.intro__button{width:50vw;letter-spacing:0.3em;height:2em;border-radius:0.5em;font-size:2em;font-weight:bold;background:linear-gradient(to bottom, #A6FFCB, #12D8FA, #1FA2FF);transition:all 0.1s}.intro__button:hover{transform:scale(1.05)}.intro__button:active{transform:scale(0.95)}.header{position:fixed;z-index:2;top:0;height:50px;width:100%;display:flex;justify-content:center;align-items:center}.header__label{text-align:center;margin:1em}.header__label_level{width:20vw}.header__label_pack{width:60vw}.header__range{width:100%}.header__range_level[type=range]::-webkit-slider-thumb{height:4vw;width:4vw;margin-top:-1.7vw}.header__range_pack[type=range]::-webkit-slider-thumb{height:3vw;width:3vw;margin-top:-1.2vw}@media (max-width: 639px){.header{height:40px}}.button{min-width:6em;min-height:3em;border:0.1em solid #000000;border-radius:1em;vertical-align:middle;text-align:center;text-transform:uppercase;font-family:"Courier New", sans-serif}.main{width:100%;min-height:100vh}@media (max-width: 639px){.main{margin-top:40px}}.content{min-height:100vh;display:flex;flex-direction:column;justify-content:center;align-items:center}.cards-container{position:relative;display:grid;grid-gap:10px;grid-template-columns:150px [scene-start] 150px 150px [scene-end] 150px;grid-template-rows:80px [scene-start] 80px 80px 80px [scene-end]}@media (max-width: 639px){.cards-container{grid-template-columns:[scene-start] 150px 150px [scene-end];grid-template-rows:[scene-start] 80px 80px 80px [scene-end];grid-auto-rows:80px;padding-top:270px}}.card{position:relative;display:flex;flex-direction:column;justify-content:center;align-items:flex-start;padding-left:1.5em;background:rgba(120,120,200,0.5);border:1px solid #000000;border-radius:0.5em;cursor:pointer}.card:before{position:absolute;content:"ðŸ”ˆ";top:calc(50% - 0.4em);left:0;font-size:1.5em;line-height:1;opacity:0.8}.card__word{margin:0;font-weight:bold}.card__transcription{margin:0}.scene{grid-area:scene;width:310px;height:260px;border:2px solid #000000;border-radius:0.5em}.scene__image{width:100%;height:220px;border:none;border-bottom:2px solid #000000;border-radius:0.5em}.scene__translation{margin:5px;padding:0;max-width:100%;overflow:hidden;font-size:1.5em;font-weight:bold;text-align:center}@media (max-width: 639px){.scene{position:fixed;z-index:2;top:40px;background:#87c8fa}}
